/* Container kryesor */
.readings-container {
  max-width: 900px;
  margin: 20px auto;
  padding: 20px;
  background: #f9fafb;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 0.1);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

/* Titujt */
.readings-container h2,
.readings-container h3 {
  color: #333;
  margin-bottom: 15px;
}

/* Forma e leximit */
.readings-form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 30px;
}

.readings-form select,
.readings-form input[type="number"] {
  flex: 1 1 150px;
  padding: 10px 12px;
  border: 1.5px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.readings-form select:focus,
.readings-form input[type="number"]:focus {
  border-color: #4f46e5; /* Indigo */
  outline: none;
}

.readings-form button {
  padding: 10px 20px;
  background-color: #4f46e5;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  flex: 1 1 120px;
  transition: background-color 0.3s ease;
}

.readings-form button:disabled {
  background-color: #a5b4fc;
  cursor: not-allowed;
}

.readings-form button:hover:not(:disabled) {
  background-color: #4338ca;
}

/* Tabela e leximeve */
.readings-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.9rem;
}

.readings-table th,
.readings-table td {
  border-bottom: 1px solid #ddd;
  padding: 8px 10px;
  text-align: left;
  color: #444;
}

.readings-table th {
  background-color: #f3f4f6;
  font-weight: 600;
}

/* Tekst i drejtë për kolonat numerike */
.readings-table td.numeric {
  text-align: right;
  font-variant-numeric: tabular-nums;
}

/* Mesazhi kur nuk ka lexime */
.no-readings-msg {
  font-style: italic;
  color: #666;
  margin-top: 10px;
}

/* Responsive: në ekran të vogël bëj scroll horizontale te tabela */
@media (max-width: 600px) {
  /* Wrapper-i i tabelës që lejon scroll horizontal */
  .readings-table-wrapper {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch; /* për scroll më të lëmuar në iOS */
  }

  /* Forma e leximeve bëhet kolone */
  .readings-form {
    flex-direction: column;
    gap: 15px; /* më shumë hapësirë midis fushave */
  }

  .readings-form select,
  .readings-form input[type="number"],
  .readings-form button {
    flex: 1 1 100%; /* zgjat në 100% gjerësi */
    max-width: 100%;
  }

  /* Stilizimi i filters për ekran të vogël */
  .filters {
    flex-direction: column;
    gap: 12px; /* shto hapësirë midis elementeve */
  }

  .filters input[type="text"],
  .filters input[type="date"] {
    max-width: 100%;
    width: 100%; /* meqë janë në kolonë, zgjaten 100% */
  }
}

.scroll-to-top-float {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 50px;
  height: 50px;
  background-color: #0077b6;
  color: white;
  font-size: 24px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.scroll-to-top-float:hover {
  background-color: #023e8a;
  transform: scale(1.1);
}
